<template>
  <div class="no-reasult-contaner">
    <i class="iconfont icon-jiazaishibai" :style="{color:colors}"></i>
    <p class="no-reasult-txt" :style="{color:colors}">{{text}}</p>
  </div>
</template>
<script>
export default {
  props: {
    colors: {
      type: String,
      default: "#666"
    },
    text: {
      type: String,
      default: "未查到结果"
    }
  }
};
</script>
<style lang="less" scoped>
.no-reasult-contaner {
  .iconfont {
    font-size: 48px;
  }
  .no-reasult-txt {
  }
}
</style>



