<template>
  <div class="content-search">
    <div class="iconfont icon-sousuo"></div>
    <input
      class="input"
      type="text"
      @keyup.enter="searchFun()"
      @change="searchFun()"
      @input="inputFun()"
      confirm-type="search"
      v-model="val"
      :placeholder="tips"
    >
    <div class="iconfont icon-quxiao" v-if="isShowClearrBtn" @click="clearFun()"></div>
  </div>
</template>

<script>
export default {
  props: {
    tips: String
  },
  data() {
    return {
      val: "",
      isShowClearrBtn: false
    };
  },
  methods: {
    clearFun() {
      this.val = "";
      this.isShowClearrBtn = false;
      this.$emit("emitClearFun");
    },
    inputFun() {
      this.isShowClearrBtn = true;
    },
    searchFun() {
      if (this.val) {
        this.$emit("emitSearchFun", this.val);
      }
    }
  },
  watch: {
    val(val) {
      this.val = this.val.toUpperCase();
      this.searchFun();
    }
  }
};
</script>

<style lang="less" scoped>
.content-search {
  display: flex;
  height: 32px;
  border-radius: 5px;
  background-color: #fff;
  flex: 1;
  align-items: center;
  padding: 0 5px;
  .icon-sousuo {
    color: #cccccc;
    font-size: 14px;
  }
  .input {
    flex: 1;
    margin: 0 5px;
    font-size: 12px;
    outline: none;
    border: none;
  }
  .icon-quxiao {
    font-size: 12px;
    color: #cccccc;
  }
}
</style>



