<template>
  <div class="content-Bill">
    <div class="content-header">
      <search @emitSearchFun="filteFun($event)" @emitClearFun="clearFun()" tips="输入搜索关键词"/>
    </div>
    <div class="content-nav">
      <div class="nav-item" :class="activeNav=='O'?'active-navItem':''" @click="switchFun('O')">已结</div>
      <div class="nav-item" :class="activeNav=='I'?'active-navItem':''" @click="switchFun('I')">未结</div>
      <div class="nav-item" :class="activeNav=='X'?'active-navItem':''" @click="switchFun('X')">撤销</div>
    </div>
    <div class="content-main">
      <div class="list" v-if="list.length>0">
        <div class="item" v-for="(item, index) in list" :key="index">
          <div class="num">{{index}}</div>
          <div class="restaurant-code">{{item.tableno}}</div>
          <div class="order-number">{{item.accnt}}</div>
          <div class="people-number">{{item.gsts}}</div>
          <div class="restaurant-name">{{item.exttableno}}</div>
        </div>
      </div>
      <div class="no-list" v-else>
        <noResult></noResult>
      </div>
    </div>
    <mainFooter activeItem="2"></mainFooter>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import search from "@/components/common/mobileOrdering/search.vue";
import mainFooter from "@/components/common/mobileOrdering/mainFooter.vue";
import noResult from "@/components/common/mobileOrdering/noResult.vue";
export default {
  components: {
    search,
    mainFooter,
    noResult
  },
  data() {
    return {
      list: [],
      tempList: [],
      activeNav: "O"
      //debounceTask: 500
    };
  },
  created() {
    this.search(this.activeNav);
  },
  methods: {
    async switchFun(val) {
      this.activeNav = val;
      this.search(this.activeNav);
    },
    async search(val) {
      let data = {
        params: {
          sta: val
        }
      };
      let res = await ajaxGet("/pos/router?method=getCheckList", data);
      if (res && res.resultCode == 0) {
        this.list = res.data;
        this.tempList = res.data;
      }
    },
    filteFun(val) {
      //this.activeNav = "-1";

      this.list = this.tempList;
      // if (this.debounceTask !== undefined) {
      //   clearTimeout(this.debounceTask);
      // }
      // this.debounceTask = setTimeout(() => {
      let filteAll = [];
      let listarr = [];
      this.list.forEach(ele => {
        if (ele.accnt.toUpperCase().indexOf(val.toUpperCase()) > -1) {
          filteAll.push(ele);
        }
        if (ele.tableno.toUpperCase().indexOf(val.toUpperCase()) > -1) {
          filteAll.push(ele);
        }
      });

      for (let i = filteAll.length - 1; i >= 0; i--) {
        let pd = false;
        listarr.forEach(opt => {
          if (
            opt.tableno == filteAll[i].tableno &&
            opt.accnt == filteAll[i].accnt
          ) {
            pd = true;
          }
        });
        if (!pd) {
          listarr.push(filteAll[i]);
        }
      }
      this.list = listarr;
      // }, 1000);
    },
    clearFun() {
      this.activeNav = "O";
      this.search(this.activeNav);
    },

    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less">
.content-Bill {
  .content-header {
    background: #393c41;
    padding: 14px 18px 7px;
  }
  .content-nav {
    display: flex;
    border-bottom: 1px solid #ddd;
    font-size: 15px;
    color: #333333;
    .nav-item {
      padding: 13px 0;
      text-align: center;
      flex: 1;
      border-bottom: 1px solid #fff;
    }
    .active-navItem {
      border-bottom-color: #c4ad68;
      color: #c4ad68;
    }
  }
  .content-main {
    font-size: 14px;
    color: #000000;
    padding-top: 10px;
    .list {
      padding-left: 15px;
      position: absolute;
      top: 96px;
      bottom: 50px;
      overflow-y: auto;
      width: 100%;
      box-sizing: border-box;
      .item {
        display: flex;
        align-items: center;
        border-bottom: 1px solid #ddd;
        padding: 15px 0;
        .num {
          width: 50px;
          text-align: left;
        }
        .restaurant-code {
          width: 50px;
          margin-right: 20px;
          color: #dd0a09;
        }
        .order-number {
          width: 200px;
        }
        .people-number {
          width: 50px;
        }
        .restaurant-name {
          width: 100px;
        }
      }
    }

    .no-list {
      margin-top: 150px;
    }
  }
}
</style>



